@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
*,
*:before,
*:after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul,
ol {
  list-style: none;
}

body {
  margin: 0;
  color: #333;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: 100%;
  max-width: 840px;
  padding: 0 1.25rem;
  margin: 0 auto;
}

/* PAGE CONTENT */

.page-top {
  margin-bottom: 2rem;
}

.page-title {
  font-size: 2.7rem;
  font-weight: 700;
  text-align: center;
  margin: 0;
}

.page-title::after {
  content: '';
  display: block;
  width: 3rem;
  border-top: 4px solid #f05455;
  margin: 1rem auto 0;
}

/* MEDIA */

@media (max-width: 575px) {
  .app-main {
    padding: 1.5rem 0;
  }
  .page-title {
